import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, Typography, Button, Menu, MenuItem } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import AlarmIcon from '@mui/icons-material/Alarm';
import top_img from '../assets/top.png';

const Header = () => {
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);

  const isMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const mobileMenuId = 'responsive-menu-id';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem onClick={handleMobileMenuClose} href="#">ホーム</MenuItem>
      <MenuItem onClick={handleMobileMenuClose} href="#">ブログ</MenuItem>
      <MenuItem onClick={handleMobileMenuClose} href="#">SNS</MenuItem>
    </Menu>
  );

  return (
    <div>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton edge="start" color="inherit" aria-label="logo">
            <img src={top_img} alt="" width="60" height="60" />
          </IconButton>
          <Typography variant="h6" style={{ flexGrow: 1 }}>
            ポートフォリオ２
          </Typography>
          <div className="desktopMenu">
            <Button color="inherit" href="#">ホーム</Button>
            <Button color="inherit" href="#">ブログ</Button>
            <Button color="inherit" href="#">SNS</Button>
          </div>
          <IconButton edge="end" color="inherit" onClick={handleMobileMenuOpen}>
            <MenuIcon />
          </IconButton>
          <IconButton edge="end" color="inherit">
            <AlarmIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
    </div>
  );
}

export default Header;
